import React, { Component } from 'react';
import Home from '../Home/Home'
import Join_cart from '../Join_cart/Join_cart'
import Shop from '../Shop/Shop'
import Cashier_desk from '../Cashier_desk/Cashier_desk'
import Bomb_box from '../Bomb_box/Bomb_box'
import Wechat_pay from '../Wechat_pay/Wechat_pay'
import Pay_success from '../Pay_success/Pay_success'
import Submit_order from '../Submit_order/Submit_order'
import {HashRouter as Router,Route,Link} from 'react-router-dom'
class Section extends Component{
  render(){
    return(
      <Router>
        <div>
          <Route exact path='/' component={Home}/>
          <Route path='/join_cart' component={Join_cart}/>
          <Route path='/shop' component={Shop}/>
          <Route path='/cashier_desk' component={Cashier_desk}/>
          <Route path='/bomb_box' component={Bomb_box}/>
          <Route path='/wechat_pay' component={Wechat_pay}/>
          <Route path='/pay_success' component={Pay_success}/>
          <Route path='/submit_order' component={Submit_order}/>
        </div>
      </Router>
    )
  }
}
export default Section



