import React, { Component } from 'react';
import './box.css'
import {HashRouter as Router,Route,Link} from 'react-router-dom'
class Box extends Component{
  state={
    hide:'block'
  }
  handleclick(e){
    e.preventDefault()
    this.setState({
      hide:'none'
    })
  }
  handlebigbox(){
    this.setState({
      hide:'none'
    })
  }
  handlesmartbox(e){
    e.stopPropagation()
    e.nativeEvent.stopImmediatePropagation()
  }
  render(){
    return(
      <div className="box" style={{display:`${this.state.hide}`}} onClick={this.handlebigbox.bind(this)}>
        <div className="wrap" onClick={this.state.handlesmartbox}>
          <div className="box_up">
            <span></span>
          <a href='#' onClick={this.handleclick.bind(this)}>X</a>
          </div>
          <div className="box_m">
            <p><br />!</p>
            <p>
              <Link className='alery' to='/pay_success'></Link>
              <a href="#"></a>
            </p>
          </div>
          <div className="other">
            <a href="./cashier_desk.html"></a>
          </div>
        </div>
      </div>
    )
  }
}
export default Box



