import React, { Component } from 'react';
import './address.css'
class Address extends Component{
  state={
    address:[
        {name1:'',
        area:'909513',
        phone:'186***5194'
      },
        {name1:'',
        area:'',
        phone:'159***1136'
      },
        {name1:'',
        area:'567211103',
        phone:'186***0734'
      },
        {name1:'',
        area:'909513',
        phone:'186***5194'
      },
        {name1:'',
        area:'',
        phone:'159***1136'
      },
        {name1:'',
        area:'567211103',
        phone:'186***0734'
      }
    ],
    chooseInd:0
  }
  handleclick=(i)=>{
    this.setState({
      chooseInd:i
    })
  }
  render(){
    const add =this.state.address.map((item,i)=>(
      <li key={i} className={this.state.chooseInd===i&&'pic'} onClick={()=>this.handleclick(i)}>
        <p>
          <i className="news1">&#xe6ee;</i>{item.name1}<br />
        <i className="news1">&#xe618;</i>{item.area}<br />
      <i className="news1">&#xe6b9;</i>{item.phone}
        </p>
        <p>
          <a href="#"></a>
          <a href="#"> <i className="news1">&#xe69d;</i></a>
          <a className='clear' href="#"><i className="news1">&#xe608;</i></a>
          <a className='mo_ren' href="#"></a>
        </p>
        <p className="box_s">
          <a href="#"></a>
          <a href="#"></a>
        </p>
      </li>
    ))
    return(
      <div className="address">
        <div className="addresses">
            <p>
              <span></span>
              <span><a href="#"><i className="address1">&#xe82c;</i></a>
              </span>
            </p>
        </div>
        <div className="news">
          <ul>{add}</ul>
        <a className='more' href="#"></a>
        </div>
      </div>
    )
  }
}
export default Address



