import React, { Component } from 'react';
import './join_cart.css';
import shop from '../../images/shop_07.jpg'
import goods from '../../images/88_03.jpg'
import {HashRouter as Router,Route,Link} from 'react-router-dom'
class Join_cart extends Component {
  state={
    pices:[
      {pic:goods},
      {pic:goods},
      {pic:goods},
      {pic:goods},
      {pic:goods}
    ]
  }
  render() {
    const images=this.state.pices.map((item,i)=>(
      <div key={i}>
          <img src={item.pic} alt="" />
        <p>
          <strong><br />BossT</strong>
        <span><b>88.00</b></span>
      <a href="javascript:;" className="shop"><i className="iconfont">&#xe7a8;</i></a>
          </p>
      </div>
    ))
    return (
      <Router>
        <div className="main">
          <div className="main_co">
              <p>!</p>
            <div className="comment clearfix">
                  <p>
                      <img src={shop} alt="" />
                      <span>Boss<br />T</span>
                      <span>iphone5 <br />XL <br />X1</span>
                  </p>
                  <p>
                    <Link className='return' to='/'></Link>
                    <Link className="account" to='/shop'></Link>
                  </p>
              </div>
              <div className="tip">
                  <span></span>
                  <div></div>
              </div>
              <div className="shop">
                  {images}
              </div>
          </div>
        </div>
      </Router>
    );
  }
}

export default Join_cart;



