import React, { Component } from 'react';
import Layout from '../Layout/Layout'
import Section from '../Section/Section'
import {HashRouter as Router} from 'react-router-dom'
import './app.css';
class App extends Component {
  render() {
    return (
      <Router>
        <div className="app">
          <Layout>
          <Section/>
          </Layout>
        </div>
      </Router>

    );
  }
}

export default App;



