import React, { Component } from 'react';
import './submit.css'
import Submit_for from '../Submit_for/Submit_for'
class Submit extends Component{
  render(){
    return(
      <div className="submit">
        <div className="down_con1">
          <p>
            <span>,</span><br />
            <span><em>24</em>,</span>
          </p>
          <p><span>168.00</span></p>
        </div>
        <div className="down_con2 ">
          <p>:12345678912345</p>
          <p>
            <span><i className="name">&#xe6ee;</i><i className="iphone">&#xe6b9;</i>186****0734</span><br />
            <span><i className="address">&#xe618;</i>567211103</span>
          </p>
        </div>
        <Submit_for/>
      </div>
    )
  }
}
export default Submit



